

.about{
    background-color: white;
    display: flex;
    margin-top: 30px;
    margin-bottom: 30px;


    .left{
        flex: 0.4;
        overflow: hidden;

        .imgContainer{
            width: 500px;
            height: 500px;
            background-color: #49abc3;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: right;
    

            img{
                height: 70%;

            }
        }
        
    }

    .right{
        flex: 0.6;
        position: relative;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;


            h1{
                font-size: 60px;
                margin: 10px 0;


            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 25px;
                margin-bottom: 20px;

                span{
                    font-size: inherit;
                    color: #e05263;
                }

                .ityped-cursor {
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                   50% {
                       opacity: 1;
                   }
                    100% {
                        opacity: 0;
                    }
                }
            }
            p{
                width: 80%;
                display: flex;
                margin-top: 5px;
            }
        }
    }

}